<template>
  <div id="lesson05" class="quizContent">
    <div v-if="!completed">
      <section class="questionArea">
        <h3>
          Question.{{ (questionIndex+1) }}
          <span>(All {{ questions.length }} questions)</span>
        </h3>
        <h4>{{ currentQuestion.question }}</h4>
        <ul class="answerList">
          <li
            v-for="(answer,index) in currentQuestion.answers"
            @click="addAnswer(index)"
          >{{ answer }}</li>
        </ul>
      </section>
    </div>
    <div v-if="completed" class="resultArea">
      <h3>Result</h3>
      <p>全{{ questions.length }}問中{{correctCount}}問正解</p>
      <section v-for="(question,index) in this.questions">
        <h4>{{index+1}}.{{ question.question }}</h4>
        <ul>
          <li v-for="answer in question.answers">{{ answer }}</li>
        </ul>
        <p class="correct" v-if="question.answer == answers[index]">正解!!</p>
        <div class="incorrect" v-else>
          <p>不正解...</p>
          <p>正解は「{{ question.answers[question.answer] }}」でした。</p>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "lesson05",
  data() {
    return {
      answers: [],
      questionIndex: 0,
      correctCount: 0,
      questions: [
        {
          question: "Photoshopについて正しい説明を選んでください。",
          answers: [
            "Adobe社製の主に画像を編集するためのソフト",
            "Apple社製のWEBデザインに特化したソフト",
            "Microsoft社製のテキストエディタ"
          ],
          answer: 0
        },
        {
          question: "レイヤー構造とは何か。",
          answers: [
            "アートワークを作成するためのキャンバスのようなもの",
            "画像を構成する１枚のシートのようなもの。複数のシートを重ねることで画像を作れる",
            "画像編集を効率化するための様々な機能を実行するショートカット"
          ],
          answer: 1
        },
        {
          question: "要素の大きさを測定するのに使えるツールはどれか",
          answers: [
            "クイック選択ツール",
            "長方形選択ツール",
            "手のひらツール",
            "フレームツール"
          ],
          answer: 1
        },
        {
          question:
            "レイヤーの表示と非表示を切り替える際は、どのマークをクリックしますか。",
          answers: ["目のマーク", "手のマーク", "顔のマーク"],
          answer: 0
        },
        {
          question: "画像の切り出しに使えるツールはどれか(*アセットは除く)",
          answers: ["スライスツール", "フレームツール", "手のひらツール"],
          answer: 0
        }
      ]
    };
  },
  methods: {
    addAnswer: function(index) {
      this.answers.push(index);
      if (this.questions.length == this.answers.length) {
        for (var i in this.answers) {
          var answer = this.answers[i];
          if (answer == this.questions[i].answer) {
            this.correctCount++;
          }
        }
      } else {
        this.questionIndex++;
      }
    }
  },
  computed: {
    currentQuestion: function() {
      return this.questions[this.questionIndex];
    },
    completed: function() {
      return this.questions.length == this.answers.length;
    }
  }
};
</script>
<style>
</style>



