<template>
  <div id="lesson01" class="quizContent">
    <div v-if="!completed">
      <section class="questionArea">
        <h3>
          Question.{{ (questionIndex+1) }}
          <span>(All {{ questions.length }} questions)</span>
        </h3>
        <h4>{{ currentQuestion.question }}</h4>
        <ul class="answerList">
          <li
            v-for="(answer,index) in currentQuestion.answers"
            @click="addAnswer(index)"
          >{{ answer }}</li>
        </ul>
      </section>
    </div>
    <div v-if="completed" class="resultArea">
      <h3>Result</h3>
      <p>全{{ questions.length }}問中{{correctCount}}問正解</p>
      <section v-for="(question,index) in this.questions">
        <h4>{{index+1}}.{{ question.question }}</h4>
        <ul>
          <li v-for="answer in question.answers">{{ answer }}</li>
        </ul>
        <p class="correct" v-if="question.answer == answers[index]">正解!!</p>
        <div class="incorrect" v-else>
          <p>不正解...</p>
          <p>正解は「{{ question.answers[question.answer] }}」でした。</p>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "lesson01",
  data() {
    return {
      answers: [],
      questionIndex: 0,
      correctCount: 0,
      questions: [
        {
          question: "フロントエンドエンジニアとは、どんな人を指しますか？",
          answers: [
            "主にユーザーの目に触れる部分(View)を作るエンジニア",
            "サーバやネットワークの設計・構築をするエンジニア",
            "機械が動く仕組み（メカニズム）を設計するエンジニア"
          ],
          answer: 0
        },
        {
          question:
            "フロントエンドとして、web制作において必須のコーディングスキルはどれでしょう。",
          answers: ["Java、swift", "HTML、CSS、Javascript", "Python"],
          answer: 1
        }
      ]
    };
  },
  methods: {
    addAnswer: function(index) {
      this.answers.push(index);
      if (this.questions.length == this.answers.length) {
        for (var i in this.answers) {
          var answer = this.answers[i];
          if (answer == this.questions[i].answer) {
            this.correctCount++;
          }
        }
      } else {
        this.questionIndex++;
      }
    }
  },
  computed: {
    currentQuestion: function() {
      return this.questions[this.questionIndex];
    },
    completed: function() {
      return this.questions.length == this.answers.length;
    }
  }
};
</script>
<style>
</style>


