<template>
  <div id="lesson02" class="quizContent">
    <div v-if="!completed">
      <section class="questionArea">
        <h3>
          Question.{{ (questionIndex+1) }}
          <span>(All {{ questions.length }} questions)</span>
        </h3>
        <h4>{{ currentQuestion.question }}</h4>
        <ul class="answerList">
          <li
            v-for="(answer,index) in currentQuestion.answers"
            @click="addAnswer(index)"
          >{{ answer }}</li>
        </ul>
      </section>
    </div>
    <div v-if="completed" class="resultArea">
      <h3>Result</h3>
      <p>全{{ questions.length }}問中{{correctCount}}問正解</p>
      <section v-for="(question,index) in this.questions">
        <h4>{{index+1}}.{{ question.question }}</h4>
        <ul>
          <li v-for="answer in question.answers">{{ answer }}</li>
        </ul>
        <p class="correct" v-if="question.answer == answers[index]">正解!!</p>
        <div class="incorrect" v-else>
          <p>不正解...</p>
          <p>正解は「{{ question.answers[question.answer] }}」でした。</p>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "lesson02",
  data() {
    return {
      answers: [],
      questionIndex: 0,
      correctCount: 0,
      questions: [
        {
          question: "HTMLは何の略称でしょう。",
          answers: [
            "Hyper Text Markup Language",
            "Hyper Text Markdown Language",
            "Hyper Text Wakeup Language"
          ],
          answer: 0
        },
        {
          question: "「このドキュメントはHTMLである」ことを宣言してください。",
          answers: [
            "<html lang=" + "en" + ">",
            "<html lang=" + "ja" + ">",
            "<!DOCTYPE html>"
          ],
          answer: 2
        },
        {
          question: "metaタグに記述してはいけないものを選んでください。",
          answers: [
            "文字コードの指定",
            "文章のタイトル(サイトの名前)",
            "ブラウザに表示される文書の本体"
          ],
          answer: 2
        },
        {
          question: "「要素（elements）」とは何を指す言葉でしょうか。",
          answers: ["「タグとその中身」", "「HTML」", "「CSS」"],
          answer: 0
        },
        {
          question: "「linkタグ」は、どういった役割を持つタグか。",
          answers: [
            "リンクをはる、リンクの出発点・到達点を指定する",
            "リンクする外部リソースを指定する",
            "文書のヘッダ情報を表す"
          ],
          answer: 1
        },
        {
          question:
            "「id属性」と「class属性」のうち、同じ属性値を設定する事ができないのはどちらか。",
          answers: ["id属性", "class属性", "どちらも設定できない"],
          answer: 0
        },
        {
          question:
            "セクションに関わるタグで誤った説明をしているものを選んでください。",
          answers: [
            "<nav>はナビゲーションであることを示す",
            "<section>は一つのセクションであることを示す",
            "<article>は余談・補足情報であることを示す"
          ],
          answer: 2
        },
        {
          question:
            "リストを表すタグのうち、「順序が関わらないリスト」を作成する際に使用するタグはなにか。",
          answers: ["dl", "ol", "ul"],
          answer: 2
        },
        {
          question:
            "フォームで「ラジオボタン」を作成する際に使用するタグと、その属性及び属性値を選んでください。",
          answers: [
            "<input style=" + "radio" + ">",
            "<input type=" + "radio" + ">",
            "<div class=" + "radio" + ">"
          ],
          answer: 1
        },
        {
          question:
            "「imgタグ」で画像を表示する際、Webで表示できる画像形式を答えなさい。",
          answers: ["jpg、psd、pdf", "jpg、png、gif", "jpg、tiff、ai"],
          answer: 1
        },
        {
          question:
            "要素には「ブロック要素」と「インライン要素」がありますが、「span」タグはこのうちどちらか。",
          answers: ["ブロック要素", "インライン要素"],
          answer: 1
        },
        {
          question: "CSSを構成する3つの値はどれでしょう。",
          answers: [
            "プロパティ、セレクタ、値",
            "タグ、セレクタ、スタイル",
            "プロパティ、ID、class"
          ],
          answer: 0
        },
        {
          question:
            "ボックスモデルのうち、「内容領域」の幅と高さを指定するプロパティを答えなさい。",
          answers: [
            "padding、margin",
            "width、height",
            "background-size、border"
          ],
          answer: 0
        },
        {
          question:
            "ボックスモデルのは「内容領域」と３つの周辺領域で構成されています。３つの周辺領域を答えなさい。",
          answers: [
            "position、top、left",
            "width、height、display",
            "padding、border、margin"
          ],
          answer: 2
        },
        {
          question:
            "「文字のサイズ」を指定したい時に使用するプロパティはなにか。",
          answers: ["size", "font-size", "font-weight"],
          answer: 1
        },
        {
          question:
            "ある要素の「背景色」を赤と指定したい時のCSSの指定を記述しなさい。",
          answers: ["background-size", "background-color", "color"],
          answer: 1
        },
        {
          question:
            "「position」プロパティにはボックスの配置方法として「相対位置」と「絶対位置」の指定方法があります。「絶対位置」で指定する際はどの値を記述すべきでしょうか。",
          answers: ["absolute", "relative", "static"],
          answer: 0
        },
        {
          question:
            "「color」プロパティは、ある部分の色を指定するプロパティです。なんの色を指定するプロパティか。",
          answers: ["文字", "背景", "border"],
          answer: 0
        },
        {
          question:
            "「display: none」が指定された要素は、ブラウザ上ではどのように表示されるか。",
          answers: ["表示", "非表示", "親要素に準ずる"],
          answer: 1
        },
        {
          question:
            "「<div class=" +
            "text" +
            "></div>」の「class属性」に対してスタイルを適用したい時のセレクタを記述しなさい。",
          answers: ["#text", "text", ".text"],
          answer: 2
        }
      ]
    };
  },
  methods: {
    addAnswer: function(index) {
      this.answers.push(index);
      if (this.questions.length == this.answers.length) {
        for (var i in this.answers) {
          var answer = this.answers[i];
          if (answer == this.questions[i].answer) {
            this.correctCount++;
          }
        }
      } else {
        this.questionIndex++;
      }
    }
  },
  computed: {
    currentQuestion: function() {
      return this.questions[this.questionIndex];
    },
    completed: function() {
      return this.questions.length == this.answers.length;
    }
  }
};
</script>
<style>
</style>



