<template>
  <div id="app" class="wrapper">
    <h1>
      <span>FRONT END</span>
      <span>COMPREHENSION</span>
      <span>TEST</span>
    </h1>
    <router-view />
    <div class="topBtn">
      <router-link to="/">◀︎ TOP</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "App"
};
if ("serviceWorker" in navigator) {
  navigator.serviceWorker.register("./service-worker.js").then(function() {
    console.log("Service Worker Registered");
  });
}
</script>

<style>
/* margin padding reset */
body,
div,
p,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
img,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select {
  margin: 0;
  padding: 0;
}

/* remove list point mark */
ul,
ol {
  list-style: none;
}

/* remove border & off descender */
img {
  border: 0;
  vertical-align: middle;
}

/* remove descender */
svg,
input,
select,
textarea,
label {
  vertical-align: middle;
}

/* set collapse */
table {
  border-collapse: collapse;
}

/* off bold */
th,
em,
strong {
  font-weight: normal;
}

/* all elements to border-box */
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/* break word for all elements */
*,
*:before,
*:after {
  word-break: break-all;
}
pre {
  white-space: pre-wrap;
}

/* Base style */
html {
  height: 100%;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Play", sans-serif;
  font-size: 13px;
  /* background-color: #fd7f58; */
  background: linear-gradient(
    45deg,
    #fd7f58 0%,
    #fd7f58 60vh,
    #3d526b 60vh,
    #3d526b 100%
  );
  line-height: 1.5;
  color: #413731;
  word-break: break-all;
  height: 100%;
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  text-indent: 0.01px; /*Firefox用*/
  text-overflow: ""; /*Firefox用*/
}
/* cording */
.wrapper {
  padding: 10vh 14px 0;
}
.wrapper h1 {
  text-align: center;
  margin-bottom: 24px;
  color: #fff;
}
.wrapper h1 span {
  display: inline-block;
}
.wrapper h2 {
  text-align: center;
  font-size: 14px;
  margin-bottom: 16px;
}
/* 問題 */
.quizContent {
  background: #fff;
  padding: 22px 28px;
  margin: 0 auto;
  border-radius: 20px;
  border: 2px solid #ccc;
  max-width: 980px;
  min-width: 292px;
  height: 60vh;
  overflow-y: scroll;
}
.questionArea {
  animation-name: animeName;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}
@keyframes animeName {
  0% {
    opacity: 0;
    transform: translate3d(0, 30px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.questionArea h3 {
  font-size: 16px;
}
.questionArea h4 {
  font-size: 14px;
  margin: 12px 0 20px;
}
.answerList li {
  font-size: 12px;
  color: #fff;
  background: #644949;
  padding: 12px 14px;
  margin-top: 12px;
  border-radius: 10px;
}
.answerList li:hover {
  opacity: 0.6;
}
.answerList li:first-child {
  margin-top: 0;
}
/* 結果 */
.resultArea {
  animation-name: animeName;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

.resultArea h3 {
  font-size: 16px;
  border-bottom: solid 2px #ddd;
  margin-bottom: 12px;
}
.resultArea h4 {
  margin-bottom: 8px;
}
.resultArea section {
  margin-top: 14px;
}
.correct {
  color: #fff;
  background: #fd7f58;
  font-weight: bold;
  font-size: 14px;
  padding: 10px 20px;
  margin-top: 8px;
  border-radius: 10px;
}
.incorrect {
  color: #fff;
  background: #3d526b;
  font-weight: bold;
  font-size: 14px;
  padding: 10px 20px;
  margin-top: 8px;
  border-radius: 10px;
}
.topBtn {
  display: block;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-top: 14px;
  padding-right: 10px;
}
</style>



