<template>
  <div id="lesson01" class="quizContent">
    <div v-if="!completed">
      <section class="questionArea">
        <h3>
          Question.{{ (questionIndex+1) }}
          <span>(All {{ questions.length }} questions)</span>
        </h3>
        <h4>{{ currentQuestion.question }}</h4>
        <ul class="answerList">
          <li
            v-for="(answer,index) in currentQuestion.answers"
            @click="addAnswer(index)"
          >{{ answer }}</li>
        </ul>
      </section>
    </div>
    <div v-if="completed" class="resultArea">
      <h3>Result</h3>
      <p>全{{ questions.length }}問中{{correctCount}}問正解</p>
      <section v-for="(question,index) in this.questions">
        <h4>{{index+1}}.{{ question.question }}</h4>
        <ul>
          <li v-for="answer in question.answers">{{ answer }}</li>
        </ul>
        <p class="correct" v-if="question.answer == answers[index]">正解!!</p>
        <div class="incorrect" v-else>
          <p>不正解...</p>
          <p>正解は「{{ question.answers[question.answer] }}」でした。</p>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "lesson01",
  data() {
    return {
      answers: [],
      questionIndex: 0,
      correctCount: 0,
      questions: [
        {
          question: "reset.cssについて正しい説明を選んでください。",
          answers: [
            "ブラウザ毎に異なるCSSをリセットするためのCSS",
            "HTMLで最後に読み込む必要があるCSS",
            "古いブラウザに対応させるためのCSS"
          ],
          answer: 0
        },
        {
          question: "floatについて誤った説明を選んでください。",
          answers: [
            "要素を浮かせて回り込ませるプロパティ",
            "使用する場合は必ずclearプロパティで解除する必要がある",
            "要素を中央に寄せるプロパティ"
          ],
          answer: 2
        },
        {
          question: "clearfixについて正しい説明を選んでください。",
          answers: [
            "擬似要素を用いてclearプロパティを使用する手法",
            "float解除用のプロパティ",
            "ブラウザに搭載された標準機能"
          ],
          answer: 0
        },
        {
          question: "position:absoluteの説明で正しいものを選びなさい。",
          answers: [
            "絶対位置への配置。スクロールしても位置が固定されたままになる",
            "絶対位置への配置。親要素にpositionプロパティでstatic以外の値を指定した場合に表示される位置が基準位置となる",
            "相対位置への配置。元の位置が基準位置となる"
          ],
          answer: 1
        },
        {
          question: "box-sizing:border-boxの説明で正しいものを選びなさい。",
          answers: [
            "ボックスサイズの算出方法。paddingとborderの幅を要素の幅と高さに含める",
            "ボックスサイズの算出方法。paddingとmarginの幅を要素の幅と高さに含める",
            "ボックスサイズの算出方法。borderの幅を要素の幅と高さに含める"
          ],
          answer: 0
        },
        {
          question:
            ":first-child擬似クラスについての説明で正しいものを選びなさい。",
          answers: [
            "兄弟要素のグループの中でその種類の最初の要素への指定",
            "最初に現れる子要素への指定",
            "親要素への指定"
          ],
          answer: 1
        },
        {
          question:
            ":first-child擬似クラスと:first-of-type擬似クラスの違いについて正しいものを選びなさい。",
          answers: [
            "対応しているブラウザが異なる",
            "囲んでいる要素のなかでの順番の数え方が異なる",
            "特に違いはない"
          ],
          answer: 1
        },
        {
          question:
            ":nth-child擬似クラスで偶数を指定する際の記述で誤ったものを選びなさい。",
          answers: ["nth-child(2n)", "nth-child(even)", "nth-child(odd)"],
          answer: 2
        }
      ]
    };
  },
  methods: {
    addAnswer: function(index) {
      this.answers.push(index);
      if (this.questions.length == this.answers.length) {
        for (var i in this.answers) {
          var answer = this.answers[i];
          if (answer == this.questions[i].answer) {
            this.correctCount++;
          }
        }
      } else {
        this.questionIndex++;
      }
    }
  },
  computed: {
    currentQuestion: function() {
      return this.questions[this.questionIndex];
    },
    completed: function() {
      return this.questions.length == this.answers.length;
    }
  }
};
</script>
<style>
</style>



