<template>
  <div id="lesson03" class="quizContent">
    <div v-if="!completed">
      <section class="questionArea">
        <h3>
          Question.{{ (questionIndex+1) }}
          <span>(All {{ questions.length }} questions)</span>
        </h3>
        <h4>{{ currentQuestion.question }}</h4>
        <ul class="answerList">
          <li
            v-for="(answer,index) in currentQuestion.answers"
            @click="addAnswer(index)"
          >{{ answer }}</li>
        </ul>
      </section>
    </div>
    <div v-if="completed" class="resultArea">
      <h3>Result</h3>
      <p>全{{ questions.length }}問中{{correctCount}}問正解</p>
      <section v-for="(question,index) in this.questions">
        <h4>{{index+1}}.{{ question.question }}</h4>
        <ul>
          <li v-for="answer in question.answers">{{ answer }}</li>
        </ul>
        <p class="correct" v-if="question.answer == answers[index]">正解!!</p>
        <div class="incorrect" v-else>
          <p>不正解...</p>
          <p>正解は「{{ question.answers[question.answer] }}」でした。</p>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "lesson03",
  data() {
    return {
      answers: [],
      questionIndex: 0,
      correctCount: 0,
      questions: [
        {
          question: "見出しの要素について正しい内容を選択してください。",
          answers: [
            "<h1>は一番大きな見出し、<h5>が一番小さな見出しになる",
            "<h1>はブロック要素である",
            "<h1>はインライン要素である"
          ],
          answer: 1
        },
        {
          question: "段落を表すタグはどれでしょうか。",
          answers: ["<p>", "<div>", "<br>"],
          answer: 0
        },
        {
          question: "HTMLのコメントアウトを選んでください。",
          answers: ["/*〜*/", "<!-- -->", "//"],
          answer: 1
        },
        {
          question:
            "<a>要素にリンクの飛先を指定する際、正しい記述方法を選んでください。",
          answers: [
            "<a link =" + " リンク先のurl " + ">",
            "<a style =" + " リンク先のurl " + ">",
            "<a href =" + " リンク先のurl " + ">"
          ],
          answer: 2
        },
        {
          question: "<img>要素について正しい説明を選んでください。",
          answers: [
            "終了タグが要らない",
            "ブロック要素である",
            "開始タグが要らない"
          ],
          answer: 0
        },
        {
          question: "リストに関して正しい説明を選んでください",
          answers: [
            "<li>要素はインライン要素である",
            "<ul>の子要素は<li>以外使用できない",
            "<li>の子要素に<ol>は使用できない"
          ],
          answer: 1
        },
        {
          question:
            "要素に全体を囲む赤色のボーダー(枠線)をつけたいです。正しい記述を選択してください。",
          answers: [
            "border:5px red;",
            "border:5px solid red;",
            "border:dashed red;"
          ],
          answer: 1
        },
        {
          question:
            "paddingで左20px、上40px、右20px、下30pxの余白をつけてください。",
          answers: [
            "padding:20px 40px 20px 30px;",
            "padding:40px 30px 20px 20px",
            "padding:40px 20px 30px 20px;"
          ],
          answer: 2
        },
        {
          question: "marginで上下20px、左右60pxの余白をつけてください。",
          answers: [
            "margin:20px 60px;",
            "margin:60px 20px;",
            "margin:20px 20px 60px;"
          ],
          answer: 0
        },
        {
          question: "<h1>と<p>に同じCSSを指定してください",
          answers: [
            "h1 p{プロパティ:値;}",
            "h1,p{プロパティ:値;}",
            "h1+p{プロパティ:値;}"
          ],
          answer: 1
        }
      ]
    };
  },
  methods: {
    addAnswer: function(index) {
      this.answers.push(index);
      if (this.questions.length == this.answers.length) {
        for (var i in this.answers) {
          var answer = this.answers[i];
          if (answer == this.questions[i].answer) {
            this.correctCount++;
          }
        }
      } else {
        this.questionIndex++;
      }
    }
  },
  computed: {
    currentQuestion: function() {
      return this.questions[this.questionIndex];
    },
    completed: function() {
      return this.questions.length == this.answers.length;
    }
  }
};
</script>
<style>
</style>



